<template>
  <div class="container">
    <!-- 背景音乐控制 -->
    <div class="music-control">
      <h2>《小船》奥尔夫伴奏编创</h2>
      <button @click="toggleMusic">{{ isMusicPlaying ? '停止音乐' : '开始音乐' }}</button>
    </div>
    <img alt="puzi" src="pz1.jpg">
   <!-- 乐器选择 -->
<div class="instrument-section">
  <h3>选择伴奏乐器：</h3>
  <div class="instrument-buttons">
    <button
      v-for="inst in instruments"
      :key="inst.name"
      :class="{ active: currentInstrument?.name === inst.name }"
      @click="selectInstrument(inst)"
    >
      <img :src="`${inst.image}`" :alt="inst.label" class="instrument-icon" />
      {{ inst.label }}
    </button>
  </div>
</div>

    <!-- 声势动作区 -->
    <div class="movement-section">
      <h3>声势动作：</h3>
      <div class="movement-buttons">
        <button @click="playSound('clap')">👏 拍手</button>
        <button @click="playSound('stomp')">🦶 跺脚</button>

      </div>
    </div>

    <!-- 状态提示 -->
    <div class="status">
      <p>🎵 背景音乐状态：{{ isMusicPlaying ? '播放中' : '已停止' }}</p>
      <p v-if="currentInstrument">🎶 当前乐器：{{ currentInstrument.label }}（{{ isInstrumentPlaying ? '演奏中' : '待机' }}）</p>
    </div>
    <router-link
  to="/three"
  class="magic-jump-button"
  active-class="active-portal"
>
  <span class="portal-icon">🚪</span>
  <span class="button-text">作业提交</span>
  <div class="portal-effects"></div>
</router-link>
  </div>
</template>

<script>
import { Howl } from 'howler'

export default {
  data () {
    return {
      isMusicPlaying: false,
      selectedInstrument: null,
      backgroundMusic: null,
      instrumentSound: null,
      instruments: [
        { name: 'bell', label: '鼓', sound: 'gu.wav', image: 'gu.jpg' },
        { name: 'triangle', label: '三角铁', sound: 'sanjiaotie.wav', image: 'sanjiaotie.jpg' },
        { name: 'woodblock', label: '木鱼', sound: 'muyu.wav', image: 'muyu.jpg' },
        { name: 'linggu', label: '铃鼓', sound: 'linggu1.wav', image: 'linggu.jpg' },
        { name: 'shagu', label: '沙鼓', sound: 'daaji.wav', image: 'shagu.jpg' }

      ],

      soundEffects: {
        clap: new Howl({ src: 'paishou.mp3' }),
        stomp: new Howl({ src: 'duojiao.wav' })

      }
    }
  },

  computed: {
    selectedInstrumentLabel () {
      return this.instruments.find(i => i.name === this.selectedInstrument)?.label || '无'
    },
    isInstrumentPlaying () {
      return this.instrumentSound?.playing() || false
    }
  },

  mounted () {
    // 初始化背景音乐
    this.backgroundMusic = new Howl({
      src: 'xiaochuang.mp3',
      loop: true,
      volume: 0.5
    })

    // 绑定键盘事件
    window.addEventListener('keydown', this.handleKeyPress)
  },

  beforeDestroy () {
    this.backgroundMusic.unload()
    window.removeEventListener('keydown', this.handleKeyPress)
  },

  methods: {
    toggleMusic () {
      if (this.isMusicPlaying) {
        this.backgroundMusic.pause()
        this.stopInstrument()
      } else {
        this.backgroundMusic.play()
        this.startInstrument()
      }
      this.isMusicPlaying = !this.isMusicPlaying
    },

    selectInstrument (inst) {
      // 切换乐器时停止当前演奏
      if (this.instrumentSound) {
        this.instrumentSound.stop()
      }

      // 初始化新乐器声音
      this.currentInstrument = inst
      this.instrumentSound = new Howl({
        src: [inst.sound],
        loop: true,
        volume: 0.8,
        onload: () => {
          if (this.isMusicPlaying) {
            this.instrumentSound.play()
          }
        }
      })
    },
    startInstrument () {
      if (this.currentInstrument && !this.isInstrumentPlaying) {
        // 对齐背景音乐播放位置
        const seek = this.backgroundMusic.seek()
        this.instrumentSound.seek(seek % this.instrumentSound.duration())
        this.instrumentSound.play()
      }
    },
    stopInstrument () {
      if (this.instrumentSound) {
        this.instrumentSound.pause()
      }
    },

    stopAllSounds () {
      this.backgroundMusic.stop()
      this.stopInstrument()
      this.isMusicPlaying = false
    },
    playSound (type) {
      if (type === 'instrument' && this.selectedInstrument) {
        const instrument = this.instruments.find(i => i.name === this.selectedInstrument)
        new Howl({ src: [instrument.sound] }).play()
      } else if (this.soundEffects[type]) {
        this.soundEffects[type].play()
      }
    },

    handleKeyPress (e) {
      switch (e.code) {
        case 'Space': // 空格键拍手
          this.playSound('clap')
          e.preventDefault()
          break
        case 'Enter': // Enter键跺脚
          this.playSound('stomp')
          break
      }
    }
  }
}
</script>

<style scoped>
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
  background: linear-gradient(135deg, #f6f9fc 0%, #e8f5ff 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  font-family: 'Comic Sans MS', '幼圆', cursive, sans-serif;
  position: relative;
  overflow: hidden;
}
.instrument-buttons button {
  flex: 1; /* 让按钮均匀分布 */
  max-width: 120px; /* 设置最大宽度，保证不会太大 */
  min-width: 100px; /* 避免缩小过多 */
  text-align: center;
}

.instrument-buttons button.active {
  border-color: #ff9a9e;
  box-shadow: 0 0 10px rgba(255, 154, 158, 0.5);
}

.instrument-buttons button:hover {
  transform: scale(1.1);
}

.instrument-icon {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
}
/* 云朵装饰效果 */

/* 音乐控制区 */
.music-control {
  text-align: center;
  margin-bottom: 40px;
  padding: 20px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.08);
  position: relative;
  z-index: 1;
}

.music-control h2 {
  color: #2c3e50;
  font-size: 28px;
  margin-bottom: 15px;
  text-shadow: 2px 2px 0px rgba(255,255,255,0.5);
}

/* 乐器图片 */
img {
  display: block;
  width: 120px;
  margin: 20px auto;
  filter: drop-shadow(0 5px 10px rgba(0,0,0,0.1));
}

/* 功能区公共样式 */
.instrument-section,
.movement-section {
  margin: 25px 0;
  padding: 20px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.08);
  border: 2px solid #e0f0ff;
}

h3 {
  color: #3498db;
  font-size: 22px;
  margin-bottom: 15px;
  text-align: center;
  position: relative;
  padding-bottom: 10px;
}

h3::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #3498db;
  border-radius: 2px;
}

/* 按钮组布局 */
.instrument-buttons{
  display: flex;  /* 让按钮横向排列 */

  flex-wrap: nowrap; /* 默认不换行 */
  gap: 15px; /* 按钮间距 */
  overflow-x: auto; /* 如果超出屏幕，则可左右滑动 */
  padding: 10px 0;
}
.movement-buttons {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
}

/* 按钮基础样式 */
button {
  padding: 12px 25px;
  font-size: 18px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

/* 默认按钮样式 */
button:not(.active) {
  background: linear-gradient(145deg, #6ee7b7 0%, #3bd1d3 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(67, 209, 179, 0.3);
}

/* 激活状态按钮 */
button.active {
  background: linear-gradient(145deg, #ff9a9e 0%, #fad0c4 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 154, 158, 0.3);
  animation: bounce 0.5s ease;
}

/* 按钮悬停效果 */
button:hover:not(.active) {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(67, 209, 179, 0.4);
}

/* 状态提示区 */
.status {
  margin-top: 30px;
  padding: 20px;
  background: rgba(255,255,255,0.9);
  border-radius: 15px;
  border: 2px dashed #b3e5fc;
  font-size: 16px;
  color: #2c3e50;
  text-align: center;
}

.status p {
  margin: 10px 0;
  padding: 8px;
  background: rgba(176, 224, 230, 0.2);
  border-radius: 8px;
}

/* 键盘按钮提示 */
.status p::before {
  content: "✨ ";
}

/* 动画效果 */
@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

/* 音乐播放状态指示 */
.is-music-playing::after {
  content: "🎵";
  margin-left: 10px;
  animation: musicNote 1s infinite;
}

@keyframes musicNote {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

/* 响应式调整 */
@media (max-width: 768px) {
  .container {
    padding: 20px;
    margin: 10px;
  }

  button {
    width: 100%;
    padding: 15px;
  }
}
/* 添加路由过渡动画 */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/* 调整原有按钮样式 */
.magic-jump-button {
  /* 保持原有样式 */
  /* 添加以下新属性 */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/* 添加传送门打开效果 */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
</style>
