<template>
    <div class="game-container">
      <!-- 音乐播放器（隐藏） -->
      <audio ref="bgm" :src="musicSrc" @timeupdate="updateTime"></audio>

      <!-- 显示节奏指示 -->
      <div class="rhythm-bar">
        <div
          v-for="(beat, index) in beats"
          :key="index"
          :class="['beat-indicator', { active: isActiveBeat(beat.time) }]"
        ></div>
      </div>

      <!-- 虚拟乐器 -->
      <div class="instrument-container">
        <button v-for="(instrument, index) in instruments"
          :key="index"
          class="instrument"
          @click="playSound(instrument)">
          {{ instrument.name }}
        </button>
      </div>

      <!-- 评分反馈 -->
      <div class="score">
        <p v-if="scoreMessage">{{ scoreMessage }}</p>
      </div>

      <!-- 开始游戏按钮 -->
      <button @click="startGame" class="start-btn">开始游戏</button>
      <router-link
  to="/one"
  class="magic-jump-button"
  active-class="active-portal"
>
  <span class="portal-icon">🚪</span>
  <span class="button-text">乐器编创</span>
  <div class="portal-effects"></div>
</router-link>
    </div>

  </template>

<script>
export default {
  data () {
    return {
      musicSrc: 'xiaochuang.mp3', // 替换为实际音乐文件
      beats: [0.5, 1, 1.5, 2, 2.5, 3, 3.5, 4, 7, 10, 13, 20, 25, 27, 33, 38, 40, 49, 55], // 4/4拍的节奏点（以秒为单位）
      clickTimes: [], // 记录用户点击的时间
      scoreMessage: '',

      instruments: [
        { name: '鼓', sound: 'gu.wav' },
        { name: '三角铁', sound: 'sanjiaotie.wav' },
        { name: '木鱼', sound: 'muyu.wav' }
      ]
    }
  },
  methods: {
    startGame () {
      this.$refs.bgm.currentTime = 0
      this.$refs.bgm.play()
      this.clickTimes = []
    },

    updateTime () {
      const currentTime = this.$refs.bgm.currentTime
      this.checkBeats(currentTime)
    },

    playSound (instrument) {
      const audio = new Audio(instrument.sound)
      audio.play()

      const clickTime = this.$refs.bgm.currentTime
      this.clickTimes.push(clickTime)

      this.evaluateTiming(clickTime)
    },

    evaluateTiming (clickTime) {
      const closestBeat = this.beats.reduce((prev, curr) =>
        Math.abs(curr - clickTime) < Math.abs(prev - clickTime) ? curr : prev
      )

      const offset = Math.abs(closestBeat - clickTime)
      if (offset < 0.1) {
        this.scoreMessage = '完美!'
      } else if (offset < 0.2) {
        this.scoreMessage = '良好!'
      } else {
        this.scoreMessage = '错误!'
      }

      setTimeout(() => {
        this.scoreMessage = ''
      }, 1000)
    },

    isActiveBeat (time) {
      return this.$refs.bgm && Math.abs(this.$refs.bgm.currentTime - time) < 0.1
    }
  }
}
</script>

  <style scoped>
  .game-container {
  text-align: center;
  padding: 30px;
  background: linear-gradient(135deg, #FFB6C1, #FFDAB9);
  min-height: 100vh;
  font-family: 'Comic Sans MS', cursive;
}

.rhythm-bar {
  display: flex;
  justify-content: space-between;
  margin: 40px auto;
  width: 80%;
  height: 30px;
  background: #FFF5E6;
  border-radius: 15px;
  padding: 0 10px;
  box-shadow: 0 5px 15px rgba(255,105,180,0.3);
  border: 3px solid #FF69B4;
}

.beat-indicator {
  width: 20px;
  height: 20px;
  background: #FF69B4;
  border-radius: 50%;
  margin: 5px 0;
  transition: all 0.2s;
  box-shadow: 0 0 10px rgba(255,105,180,0.5);
}

.beat-indicator.active {
  background: #FFF200;
  transform: scale(1.3);
  box-shadow: 0 0 20px #FFF200;
}

.instrument-container {
  display: flex;
  justify-content: center;
  margin: 40px 0;
}

.instrument {
  margin: 15px;
  padding: 20px 25px;
  font-size: 20px;
  background: linear-gradient(145deg, #FF6B6B, #FF8E53);
  border: none;
  border-radius: 15px;
  cursor: pointer;
  color: white;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.3);
  box-shadow: 0 5px 15px rgba(255,107,107,0.4);
  transition: all 0.2s;
}

.instrument:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(255,107,107,0.6);
}

.instrument:active {
  transform: scale(0.95);
}

.score {
  margin: 30px 0;
  font-size: 28px;
  font-weight: bold;
  color: #FFD700;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
  animation: pop 0.3s ease;
}

@keyframes pop {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.start-btn {
  margin-top: 30px;
  padding: 15px 40px;
  font-size: 24px;
  background: linear-gradient(145deg, #FF6B6B, #FF8E53);
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.3);
  box-shadow: 0 5px 15px rgba(255,107,107,0.4);
  transition: all 0.2s;
}

.start-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(255,107,107,0.6);
}

/* 添加卡通云朵装饰 */
.game-container::before {
  content: '';
  position: absolute;
  top: 10%;
  left: 5%;
  width: 100px;
  height: 50px;
  background: white;
  border-radius: 50px;
  opacity: 0.3;
  filter: blur(5px);
}
/* 添加路由过渡动画 */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/* 调整原有按钮样式 */
.magic-jump-button {
  /* 保持原有样式 */
  /* 添加以下新属性 */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/* 添加传送门打开效果 */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
  </style>
