<template>
  <!-- 在原有模板基础上增加动画类和装饰元素 -->
  <div id="game" class="animated-entry">
    <div class="wave-bg"></div> <!-- 新增波浪背景 -->
    <h1>
      <span class="music-note">♪</span>
      跟随模仿游戏: 《小船》
      <span class="music-note">♪</span>
    </h1>

    <!-- 原有audio元素保持不变 -->
    <audio ref="audio" :src="song" preload="auto"></audio>
    <div v-if="!gameStarted" class="start-screen">
      <button @click="startGame" class="start-button pulse">
        🎵 开始音乐游戏
      </button>
    </div>

    <div v-else class="game-area">
      <div class="floating-text">
        🎶 听到旋律后，站起来模仿角色的动作吧! 🎶
      </div>

      <!-- 动作展示区改为卡片式 -->
      <div class="action-card">
        <div class="action-header">
          <span class="animated-icon">💃</span>
          当前动作展示
          <span class="animated-icon">🕺</span>
        </div>
        <transition name="fade" mode="out-in">
          <div key="currentAction" class="action-content">
            <p class="action-description">{{ currentActionDescription }}</p>
            <img :src="currentActionImage"
                 class="action-image bounce"
                 alt="模仿动作示范"/>
          </div>
        </transition>
      </div>

      <button @click="stopGame" class="stop-button hover-shake">
        ⏹ 停止游戏
      </button>
    </div>
    <router-link
  to="/createtwo"
  class="magic-jump-button"
  active-class="active-portal"
>
  <span class="portal-icon">🚪</span>
  <span class="button-text">唱歌方法</span>
  <div class="portal-effects"></div>
</router-link>
  </div>
</template>

<script>
export default {
  data () {
    return {
      song: 'xiaochuang.mp3', // 确保路径正确
      gameStarted: false,
      actions: [
        { image: 'guzhang.jpg', description: '这是鼓掌动作' }, // 动作图像和描述
        { image: 'paizhuo.jpg', description: '这是拍桌子的动作' }
      ],
      currentAction: 0,
      actionInterval: null
    }
  },
  computed: {
    currentActionImage () {
      return this.actions[this.currentAction].image // 返回当前动作的图像路径
    },
    currentActionDescription () {
      return this.actions[this.currentAction].description // 返回当前动作的描述
    }
  },
  methods: {
    startGame () {
      this.gameStarted = true
      this.currentAction = 0
      this.playSong()
      this.showActions()
    },
    playSong () {
      this.$refs.audio.play() // 播放旋律
    },
    showActions () {
      this.actionInterval = setInterval(() => {
        // 切换动作
        this.currentAction = (this.currentAction + 1) % this.actions.length
      }, 5000) // 每5秒切换动作
    },
    stopGame () {
      this.gameStarted = false
      this.$refs.audio.pause() // 停止音频
      if (this.actionInterval) {
        clearInterval(this.actionInterval) // 清除动作切换
      }
    }
  },
  beforeDestroy () {
    // 确保在组件销毁时清除定时器
    if (this.actionInterval) {
      clearInterval(this.actionInterval)
    }
  }
}
</script>

<style scoped>
#game {
  position: relative;
  max-width: 800px;
  margin: 2rem auto;
  padding: 2.5rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-radius: 1.5rem;
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
  overflow: hidden;
}

.wave-bg {
  position: absolute;
  bottom: -10%;
  left: 0;
  width: 100%;
  height: 100px;
  background: url('data:image/svg+xml;utf8,<svg viewBox="0 0 1200 100" xmlns="http://www.w3.org/2000/svg"><path d="M0 70l50-5c50-5 150-15 250-15s200 10 300 20 250 20 350 10 200-20 300-25l50-5v100H0z" fill="%2343a04788"/></svg>');
  opacity: 0.3;
  animation: wave 12s linear infinite;
}

@keyframes wave {
  0% { transform: translateX(-100px); }
  100% { transform: translateX(0); }
}

h1 {
  font-family: '幼圆', cursive;
  font-size: 2.8rem;
  color: #2c3e50;
  margin-bottom: 2rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.music-note {
  font-size: 3rem;
  margin: 0 1rem;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.start-screen {
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  font-size: 1.2rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  position: relative;
  overflow: hidden;
}

button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.1);
  opacity: 0;
  transition: opacity 0.3s;
}

button:hover:before {
  opacity: 1;
}

.start-button {
  background: linear-gradient(45deg, #43a047, #2e7d32);
  box-shadow: 0 5px 15px rgba(67,160,71,0.3);
}

.stop-button {
  background: linear-gradient(45deg, #e53935, #c62828);
  box-shadow: 0 5px 15px rgba(229,57,53,0.3);
}

.action-card {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  margin: 2rem 0;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  transform-style: preserve-3d;
}

.action-header {
  font-size: 1.5rem;
  color: #43a047;
  font-weight: bold;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.animated-icon {
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.action-image {
  width: 100px;
  border-radius: 1rem;
  border: 3px solid #43a047;
  padding: 0.5rem;
  background: white;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: transform 0.3s;
}

.action-image:hover {
  transform: rotate(-3deg) scale(1.05);
}

.bounce {
  animation: bounce 2s ease infinite;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s, transform 0.5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
  transform: translateY(10px);
}

.hover-shake:hover {
  animation: shake 0.5s ease;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

.animated-entry {
  animation: zoomIn 0.6s ease;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
/* 添加路由过渡动画 */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/* 调整原有按钮样式 */
.magic-jump-button {
  /* 保持原有样式 */
  /* 添加以下新属性 */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/* 添加传送门打开效果 */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
</style>
