<template>
<div class="piano-container">
  <button
      v-if="!audioActivated"
      class="activate-btn"
      @click="activateAudio"
    >
      🎹 点击激活钢琴
    </button>
    <!-- 钢琴键盘 -->
    <div v-else  class="piano">
      <div
        v-for="(key, index) in whiteKeys"
        :key="index"
        class="key white"
        :class="{ active: key.active }"
        @mousedown="playNote(key.note)"
        @mouseup="releaseNote(key.note)"
        @mouseleave="releaseNote(key.note)"
      >
        <span class="key-label">{{ key.keyboard }}</span>
      </div>

      <div
        v-for="(key, index) in blackKeys"
        :key="'b'+index"
        class="key black"
        :class="{ active: key.active }"
        :style="{ left: key.position + '%' }"
        @mousedown="playNote(key.note)"
        @mouseup="releaseNote(key.note)"
        @mouseleave="releaseNote(key.note)"
      >
        <span class="key-label">{{ key.keyboard }}</span>
      </div>
    </div>
    <router-link
  to="/createthree"
  class="magic-jump-button"
  active-class="active-portal"
>
  <span class="portal-icon">🚪</span>
  <span class="button-text">火车小游戏</span>
  <div class="portal-effects"></div>
</router-link>
  </div>
</template>

<script>

export default {

  name: 'SIngpage',
  data () {
    return {
      show: true,
      whiteKeys: [
        { note: 'C4', active: false },
        { note: 'D4', active: false },
        { note: 'E4', active: false },
        { note: 'F4', active: false },
        { note: 'G4', active: false },
        { note: 'A4', active: false },
        { note: 'B4', active: false }
      ],
      blackKeys: [
        { note: 'C#4', position: 12.5, active: false },
        { note: 'D#4', position: 25, active: false },
        { note: 'F#4', position: 50, active: false },
        { note: 'G#4', position: 62.5, active: false },
        { note: 'A#4', position: 75, active: false }
      ],
      audioContext: null,
      audioActivated: false
    }
  },
  mounted () {
    // 初始化音频上下文
    this.audioContext = new (window.AudioContext || window.webkitAudioContext)()
    // 绑定键盘事件
    window.addEventListener('keydown', this.handleKeyDown)
    window.addEventListener('keyup', this.handleKeyUp)
  },
  beforeDestroy () {
    window.removeEventListener('keydown', this.handleKeyDown)
    window.removeEventListener('keyup', this.handleKeyUp)
  },
  methods: {
    async activateAudio () {
      this.audioContext = new (window.AudioContext || window.webkitAudioContext)()
      this.audioActivated = true
    },
    // 播放音符
    playNote (note) {
      this.setKeyActive(note, true)
      this.playSound(note)
    },
    // 停止音符
    releaseNote (note) {
      this.setKeyActive(note, false)
    },
    // 设置琴键激活状态
    setKeyActive (note, isActive) {
      const whiteKey = this.whiteKeys.find(k => k.note === note)
      if (whiteKey) whiteKey.active = isActive

      const blackKey = this.blackKeys.find(k => k.note === note)
      if (blackKey) blackKey.active = isActive
    },
    // 生成声音（简单实现）
    playSound (note) {
      const oscillator = this.audioContext.createOscillator()
      const gainNode = this.audioContext.createGain()

      oscillator.connect(gainNode)
      gainNode.connect(this.audioContext.destination)

      // 根据音符设置频率（示例频率）
      const frequencies = {
        C4: 261.63,
        'C#4': 277.18,
        D4: 293.66,
        'D#4': 311.13,
        E4: 329.63,
        F4: 349.23,
        'F#4': 369.99,
        G4: 392.00,
        'G#4': 415.30,
        A4: 440.00,
        'A#4': 466.16,
        B4: 493.88
      }

      oscillator.frequency.value = frequencies[note] || 440
      oscillator.type = 'sine' // 波形类型：sine, square, sawtooth, triangle

      gainNode.gain.setValueAtTime(0.5, this.audioContext.currentTime)

      oscillator.start()
      // 停止声音（简单实现）
      setTimeout(() => {
        oscillator.stop()
        gainNode.disconnect()
      }, 500)
    },
    // 处理键盘按下
    handleKeyDown (event) {
      const key = event.key.toUpperCase()
      const whiteKey = this.whiteKeys.find(k => k.keyboard === key)
      const blackKey = this.blackKeys.find(k => k.keyboard === key)

      if (whiteKey && !whiteKey.active) this.playNote(whiteKey.note)
      if (blackKey && !blackKey.active) this.playNote(blackKey.note)
    },
    // 处理键盘释放
    handleKeyUp (event) {
      const key = event.key.toUpperCase()
      const whiteKey = this.whiteKeys.find(k => k.keyboard === key)
      const blackKey = this.blackKeys.find(k => k.keyboard === key)

      if (whiteKey) this.releaseNote(whiteKey.note)
      if (blackKey) this.releaseNote(blackKey.note)
    }
  }
}
</script>

<style lang="less" scoped>
@piano-bg: #2c3e50;
@key-shadow: 0 5px 15px rgba(0,0,0,0.2);

.piano-container {
  background: linear-gradient(45deg, darken(@piano-bg, 10%), @piano-bg);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.activate-btn {
  padding: 1rem 2rem;
  font-size: 1.2rem;
  background: #fff;
  border: none;
  border-radius: 30px;
  box-shadow: @key-shadow;
  cursor: pointer;
  transition: transform 0.2s;

  &:hover {
    transform: scale(1.05);
  }
}

.piano {
  position: relative;
  display: flex;
  width: 20vw;
  max-width: 1000px;
  min-width: 300px;
  height: 25vh;
  min-height: 150px;
  background: #1a1a1a;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.3);
}

.key {
  position: relative;
  cursor: pointer;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);

  &.active {
    transform: translateY(2px);
  }
}

.white {
width:14%;
  flex: 1;
  background: linear-gradient(to bottom, #fff 0%, #f8f8f8 100%);
  border: 1px solid #ddd;
  border-radius: 0 0 8px 8px;
  margin: 0 0.5%;
  box-shadow: @key-shadow;

  &.active {
    background: linear-gradient(to bottom, #eee 0%, #e8e8e8 100%);
  }
}

.black {
  position: absolute;
  top: 0;
  width: 7%;
  height: 50%;
  background: linear-gradient(45deg, #222, #000);
  border-radius: 0 0 5px 5px;
  z-index: 1;
  box-shadow: @key-shadow;

  &.active {
    background: linear-gradient(45deg, #333, #111);
  }
}

.key-label {
  position: absolute;
  bottom: 12px;
  width: 100%;
  text-align: center;
  font-family: 'Arial Rounded MT Bold', Arial, sans-serif;
  font-size: 1.1vw;
  color: #666;
  user-select: none;

  .black & {
    color: rgba(255,255,255,0.8);
  }
}

@media (max-width: 768px) {
  .piano {
    width: 95vw;
    height: 30vh;
  }

  .key-label {
    font-size: 2.5vw;
  }
}
/* 添加路由过渡动画 */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/* 调整原有按钮样式 */
.magic-jump-button {
  /* 保持原有样式 */
  /* 添加以下新属性 */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/* 添加传送门打开效果 */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
</style>
