<template>
  <div>
    <input type="file" @change="onFileUpload" accept="image/*">
    <div v-if="essay">
      <h3>生成的小作文：</h3>
      <p>{{ essay }}</p>
    </div>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs'
import * as mobilenet from '@tensorflow-models/mobilenet'

export default {
  name: 'AI',
  data () {
    return {
      model: null,
      essay: ''
    }
  },
  async mounted () {
    await tf.ready() // 初始化 TensorFlow
    this.model = await mobilenet.load()
  },
  methods: {
    async onFileUpload (event) {
      const file = event.target.files[0]
      if (!file) return

      // 1. 加载图片并分类
      const img = await this.loadImage(file)
      const predictions = await this.model.classify(img)

      // 2. 提取前3个最可能的类别
      const top3 = predictions.slice(0, 3)
        .map(pred => ({
          name: pred.className.replace(/,/g, '、'), // 处理分类名称中的逗号
          prob: Math.round(pred.probability * 100)
        }))

      // 3. 根据分类结果生成模板化小作文
      this.generateEssay(top3)
    },

    loadImage (file) {
      return new Promise((resolve) => {
        const reader = new FileReader()
        reader.onload = (e) => {
          const img = new Image()
          img.src = e.target.result
          img.onload = () => resolve(img)
        }
        reader.readAsDataURL(file)
      })
    },

    generateEssay (categories) {
      // 模板化生成文本（示例）
      this.essay = `这张图片让我联想到${categories[0].name}。从细节来看，画面中可能有${categories[1].name}和${categories[2].name}的元素。\
        整体风格给人一种${this.getAdjective(categories[0].prob)}的感觉，\
        尤其是${categories[0].name}的部分，可能性高达${categories[0].prob}%。\
        这让我不禁想起${this.getRandomTheme()}的场景，真是令人印象深刻！`
    },

    // 辅助函数：根据置信度返回形容词
    getAdjective (prob) {
      return prob > 90 ? '非常鲜明' : prob > 70 ? '较为清晰' : '朦胧神秘'
    },

    // 辅助函数：随机主题
    getRandomTheme () {
      const themes = ['夏日海滩', '城市夜景', '自然风光', '历史建筑']
      return themes[Math.floor(Math.random() * themes.length)]
    }
  }
}
</script>
