import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import kechengView from '@/views/kechengView.vue'
import AboutView from '@/views/AboutView.vue'
import OneView from '@/views/oneView.vue'
import TwoView from '@/views/twoView.vue'
import ThreeView from '@/views/threeView.vue'
import XinViewone from '@/views/xinViewone.vue'
import CreateViewone from '@/views/createViewone.vue'
import SingViewtwo from '@/views/singViewtwo.vue'
import XinViewTwo from '@/views/xinView.two.vue'
import CreateViewtwo from '@/views/createViewtwo.vue'
import SingViewthree from '@/views/singViewthree.vue'
import XinViewthree from '@/views/xinViewthree.vue'
import CreateViewthree from '@/views/createViewthree.vue'

import ChinaMap from '@/views/ChinaMap.vue'
import ProvinceDetail from '@/views/ProvinceDetail.vue'
import KJ from '@/views/KJ.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/chinamap',
    component: ChinaMap
  },
  {
    path: '/province/:name',
    name: 'ProvinceDetail',
    component: ProvinceDetail,
    props: true

  },
  {
    path: '/kj',
    name: 'kj',
    component: KJ
  },
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: AboutView
  },
  {
    path: '/kecheng',
    name: 'kecheng',
    component: kechengView
  },

  {
    path: '/xinone',
    name: 'xinone',
    component: XinViewone
  },
  {
    path: '/createone',
    name: 'createone',
    component: CreateViewone
  },
  {
    path: '/one',
    name: 'one',
    component: OneView
  },
  {
    path: '/singtwo',
    name: 'singtwo',
    component: SingViewtwo
  },
  {
    path: '/xintwo',
    name: 'xintwo',
    component: XinViewTwo
  },
  {
    path: '/createtwo',
    name: 'createtwo',
    component: CreateViewtwo
  },
  {
    path: '/two',
    name: 'two',
    component: TwoView
  },
  {
    path: '/singthree',
    name: 'singthree',
    component: SingViewthree
  },
  {
    path: '/xinthree',
    name: 'xinthree',
    component: XinViewthree
  },
  {
    path: '/createthree',
    name: 'createthree',
    component: CreateViewthree
  },
  {
    path: '/three',
    name: 'three',
    component: ThreeView

  }
]

const router = new VueRouter({
  routes,
  mode: 'hash'
})

export default router
