<template>
  <div>
    <button @click="toggleNav" class="toggle-nav-btn">
      {{ isNavVisible ? '隐藏导航' : '显示导航' }}
    </button>
     <div class="left-sound-bar" v-show="isNavVisible">
    <div
      v-for="sound in sounds"
      :key="sound.id"
      class="nav-sound-item"
      :class="{ playing: sound.isPlaying }"
    >
      <div class="nav-button-wrapper">
        <!-- 圆形按钮 -->
        <div
          class="nav-play-button"
          :style="{ backgroundImage: `url(${sound.imageUrl})` }"
          @click="togglePlay(sound)"
        >
          <div class="nav-play-icon">
            {{ sound.isPlaying ? '⏸' : '▶' }}
          </div>
        </div>

        <!-- 扩展控制区域 -->
        <transition name="slide">
          <div class="nav-controls" v-show="sound.isPlaying">
            <input
              type="range"
              min="0"
              max="100"
              v-model.number="sound.volume"
              @input="updateVolume(sound)"
              class="nav-volume-slider"
            >
            <span class="sound-name">{{ sound.name }}</span>
          </div>
        </transition>
      </div>
    </div>
  </div>
  <div class="image-display-area">
      <transition-group name="image-fade">
        <div
          v-for="image in displayedImages"
          :key="image.id"
          class="floating-image"
          :style="{
            left: image.position.x + 'px',
            top: image.position.y + 'px',
            zIndex: image.zIndex
          }"
          @mousedown="startDrag(image, $event)"
          @touchstart.prevent="startDrag(image, $event)"
        >
          <img
            :src="image.url"
            alt="音效图片"
            class="display-image"
          >
          <div class="close-btn" @click="removeImage(image.id)">×</div>
        </div>
      </transition-group>
    </div>
  </div>

</template>

<script>
let zIndexCounter = 1000
export default {
  name: 'YInxiao',
  data () {
    return {
      isNavVisible: true,
      displayedImages: [],
      dragState: {
        isDragging: false,
        currentImage: null,
        startX: 0,
        startY: 0
      },
      sounds: [
        {
          id: 1,
          name: '风声',
          file: 'wind.mp3',
          imageUrl: 'wind.jpg', // 图片路径
          isPlaying: false,
          volume: 50,
          audio: null
        }, {
          id: 2,
          name: '鸟鸣声',
          file: 'birds.mp3',
          imageUrl: 'bird.jpg', // 图片路径
          isPlaying: false,
          volume: 50,
          audio: null
        },
        {
          id: 3,
          name: '海浪声',
          file: 'sea1.mp3',
          imageUrl: 'sea.jpg', // 图片路径
          isPlaying: false,
          volume: 50,
          audio: null
        }
        // 其他音效配置...
      ]

    }
  },

  mounted () {
    this.initializeAudio()
  },

  beforeDestroy () {
    this.stopAll()
  },

  methods: {
    toggleNav () {
      this.isNavVisible = !this.isNavVisible
    },
    initializeAudio () {
      this.sounds.forEach(sound => {
        sound.audio = new Audio(sound.file)
        sound.audio.volume = sound.volume / 100
        sound.audio.addEventListener('ended', () => {
          sound.isPlaying = false
        })
      })
    },

    togglePlay (sound) {
      if (!sound.isPlaying) {
        this.addImageToDisplay(sound.imageUrl)
      }
      if (sound.isPlaying) {
        sound.audio.pause()
      } else {
        sound.audio.play()
      }
      sound.isPlaying = !sound.isPlaying
      const button = event.currentTarget
      const ripple = document.createElement('div')
      ripple.className = 'ripple'
      button.appendChild(ripple)
      setTimeout(() => ripple.remove(), 600)
    },

    updateVolume (sound) {
      sound.audio.volume = sound.volume / 100
    },

    stopAll () {
      this.sounds.forEach(sound => {
        if (sound.audio) {
          sound.audio.pause()
          sound.audio.currentTime = 0
        }
      })
    },
    addImageToDisplay (imageUrl) {
      const viewportWidth = window.innerWidth
      const viewportHeight = window.innerHeight

      this.displayedImages.push({
        id: Date.now() + Math.random(),
        url: imageUrl,
        position: {
          x: Math.random() * (viewportWidth - 200),
          y: Math.random() * (viewportHeight - 200)
        },
        zIndex: zIndexCounter++
      })
    },

    removeImage (id) {
      this.displayedImages = this.displayedImages.filter(img => img.id !== id)
    },

    startDrag (image, event) {
      const { clientX, clientY } = event.touches ? event.touches[0] : event
      this.dragState = {
        isDragging: true,
        currentImage: image,
        startX: clientX - image.position.x,
        startY: clientY - image.position.y
      }

      document.addEventListener('mousemove', this.handleDrag)
      document.addEventListener('mouseup', this.stopDrag)
      document.addEventListener('touchmove', this.handleDrag)
      document.addEventListener('touchend', this.stopDrag)
    },

    handleDrag (event) {
      if (!this.dragState.isDragging) return

      event.preventDefault()
      const { clientX, clientY } = event.touches ? event.touches[0] : event

      const newX = clientX - this.dragState.startX
      const newY = clientY - this.dragState.startY

      this.displayedImages = this.displayedImages.map(img => {
        if (img.id === this.dragState.currentImage.id) {
          return {
            ...img,
            position: {
              x: Math.max(0, Math.min(newX, window.innerWidth - 200)),
              y: Math.max(0, Math.min(newY, window.innerHeight - 200))
            },
            zIndex: zIndexCounter++
          }
        }
        return img
      })
    },

    stopDrag () {
      this.dragState.isDragging = false
      document.removeEventListener('mousemove', this.handleDrag)
      document.removeEventListener('mouseup', this.stopDrag)
      document.removeEventListener('touchmove', this.handleDrag)
      document.removeEventListener('touchend', this.stopDrag)
    }

  }
}

</script>

<style lang="less" scoped>
.image-display-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

.floating-image {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.2);
  cursor: move;
  pointer-events: auto;
  transition: transform 0.3s ease;
  width: min(200px, 30vw);  // 最大200px，小屏幕按视口比例缩小
  height: min(200px, 30vw);
  max-width: 80%;  // 防止极端小屏幕溢出
  max-height: 80%;
}

.floating-image:hover {
  transform: scale(1.05) rotate(2deg);
}

.display-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.close-btn {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 24px;
  height: 24px;
  background: #ff4444;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
}

.image-fade-enter-active, .image-fade-leave-active {
  transition: all 0.5s ease;
}
.image-fade-enter, .image-fade-leave-to {
  opacity: 0;
  transform: translateY(20px);
}
.left-sound-bar {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: clamp(60px, 6vw, 80px); /* 动态宽度 */
  z-index: 1000;
  background: rgba(255,255,255,0.9);
  border-radius:15px 0 0 15px;
  box-shadow:  -2px 0 10px rgba(0,0,0,0.1);
  padding: 1.5vh 0.5vw; /* 使用视口单位 */
  transition: all 0.3s ease;
}

.left-sound-bar:hover {
  width: clamp(180px, 18vw, 220px); /* 动态扩展宽度 */
  transform: translateY(-50%) translateX(-10px);
}

.nav-sound-item {
  margin: 1.5vh 0; /* 使用视口高度单位 */
}

.nav-play-button {
  width: clamp(40px, 5vw, 60px); /* 动态按钮尺寸 */
  height: clamp(40px, 5vw, 60px);
  box-shadow: 1px 1px 6px rgba(0,0,0,0.2);
  background-size: cover;
  background-position: center top 20%; /* 突出显示图片主要内容 */
  overflow: hidden;
  position: relative;
}
.nav-play-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    rgba(0,0,0,0.1) 0%,
    rgba(0,0,0,0) 100%
  );
  pointer-events: none;
}

.nav-volume-slider {
  width: clamp(80px, 10vw, 120px); /* 动态滑块宽度 */
}

/* 小屏幕适配 */
@media (max-width: 1024px) {
  .left-sound-bar {
    width: 50px;
    padding: 10px 5px;
  }

  .left-sound-bar:hover {
    width: 160px;
  }

  .nav-play-button {
    width: 40px;
    height: 40px;
    min-width: 44px;
    min-height: 44px;
    transform: translateZ(0);
    will-change: transform;
  }

  .nav-play-icon {
    font-size: 1.2em;
  }

  .nav-volume-slider {
    width: 80px;
  }
}

/* 超小屏幕适配 */
@media (max-width: 768px) {
  .left-sound-bar {
    top: auto;
    bottom: 0;
    width: 100%;
    height: 60px;
    border-radius: 15px 15px 0 0;
    flex-direction: row;
    overflow-x: auto;
  }

  .left-sound-bar:hover {
    width: 100%;
    height: 80px;
  }

  .nav-sound-item {
    margin: 0 10px;
    flex-shrink: 0;
  }

  .nav-button-wrapper {
    flex-direction: column;
  }

  .nav-controls {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (max-width: 1024px) {
  /* 缩小整体尺寸 */
  .left-sound-bar { width: 50px }
  /* 调整按钮大小 */
  .nav-play-button { width: 40px }
}

/* 平板/手机适配 */
@media (max-width: 768px) {
  /* 改为底部栏布局 */
  .left-sound-bar {
    bottom: 0;
    width: 100%;
    height: 60px;
  }
}
@media (-webkit-min-device-pixel-ratio: 2) {
  .nav-play-icon {
    /* 高清屏优化 */
    text-shadow: 0 1px 3px rgba(0,0,0,0.4);
  }
}
</style>
